import React, {Component} from "react"
import {Map,TileLayer,GeoJSON} from "react-leaflet"
import "./mapComponent.css"
import {parkingIcon} from "../icons"

class MapComp extends Component{
    constructor(props){
        super(props)
        this.state = {zoom:15}
    }
    getKey(){
        return '_' + Math.random().toString(36).substr(2, 9);
    }

    getCoordinates(geojson){
        if (geojson != null){
            return geojson.geometry.coordinates.slice().reverse();
        }
        else{
            return null
        }
    }
    onEachFeature(feature, layer) {
        const popupContent = `<Popup><p>Address:${feature.properties.name} <br/><a href="http://www.ahuzot.co.il/Parking/ParkingDetails/?ID=${feature.properties.parkingid}">Details</a><br/><a href = "https://www.waze.com/ul/?q=${feature.properties.name}">Navigate</a></p></Popup>`
        layer.bindPopup(popupContent)
      }
    render(){
        return (
            <div id="mapDiv">
                <Map center = {this.getCoordinates(this.props.searchPoint)} zoom = {this.state.zoom}>
                    <TileLayer attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
                    <GeoJSON key={this.getKey(this.props.searchPoint)} data={this.props.searchPoint}/>
                    <GeoJSON key={this.getKey(this.props.parkings)} onEachFeature = {this.onEachFeature} pointToLayer = {parkingIcon} data={this.props.parkings}/>
                </Map>
            </div>
        )
    }
}

export default MapComp;