import React, {Component} from "react"
import "./panelComponent.css"
import Map from "./mapComponent"
import DetailCard from "./detailCardComponent"

class Panel extends Component{
    render(){
        return (
            <div className="container">
                <div className="row justify-content-sm-center">
                    <div className="col col-sm-9 mt-3 mb-3">
                        <nav class="navbar-default">
                            <div class="nav nav-tabs" id="nav-tab" role="tablist">
                                <a class="nav-item nav-link active" id="nav-map-tab" data-toggle="tab" href="#nav-map"
                                    role="tab" aria-controls="nav-map" aria-selected="true">Map</a>
                                <a class="nav-item nav-link" id="nav-detail-tab" data-toggle="tab" href="#nav-detail"
                                    role="tab" aria-controls="nav-detail" aria-selected="false">Details</a>
                            </div>
                        </nav>
                        <div class="tab-content" id="nav-tabContent">
                            <div class="tab-pane fade show active" id="nav-map" role="tabpanel"
                                aria-labelledby="nav-map-tab"><Map searchPoint = {this.props.searchPoint}  parkings = {this.props.parkings}></Map></div>
                            <div class="tab-pane fade" id="nav-detail" role="tabpanel"
                                aria-labelledby="nav-detail-tab"><DetailCard parkings = {this.props.parkings}></DetailCard></div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Panel;