import * as $ from "jquery"

let serviceUrl = "/status"

class ParkingStatus {
    async getByID(id){
        let response = await $.get(serviceUrl+"/"+id).then((res)=>{return res})
        return response
    }
    async isFree(id){
       let status = await this.getByID(id);
       return status === "free";
    }
}

export default new ParkingStatus()