import React, {Component} from "react"
import {Container,Row,Card} from "react-bootstrap"
import "./detailCardComponent.css"

class DetailCard extends Component{
    constructor(props){
        super(props)
        this.state = {}
        this.renderCard = this.renderCard.bind(this)
    }
    renderCard(){
        if (this.props.parkings != null){
            let sortedParks = this.props.parkings.features.sort((a,b) => { return a.properties.distance - b.properties.distance })
            let details = sortedParks.map((item) => {
                let name = item.properties.name
                let distance = item.properties.distance
                let url = "http://www.ahuzot.co.il/Parking/ParkingDetails/?ID="+item.properties.parkingid
                let waze = "https://www.waze.com/ul/?q=" + name
                return(
                    <Row className = "mb-5">
                        <Card style={{width : "100%"}}>
                            <Card.Body>
                                <Card.Text>
                                <ul class = "list-unstyled">
                                    <li>{name}</li>
                                    <li>{distance} Meters</li>
                                    <li><a href = {url}>More Details</a></li>
                                    <li><a href = {waze}>Navigate</a></li>
                                </ul>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Row>
                )
            })
            return details
        }
        else return null;
    }
    render(){
        return (
            <Container className = "details">
                <div className = "col col-sm-12">
                    {this.renderCard()}
                </div>
            </Container>
        )
    }

}

export default DetailCard;