import React, {Component} from 'react';
import './App.css';
import Search from "./components/searchComponent"
import Panel from "./components/panelComponent"

class App extends Component{
  constructor(props){
    super(props);
    this.state = {
      searchPoint : null,
      parkings : null
    }
    this.handler = this.handler.bind(this);
  }
  handler(props){
    this.setState(props);
  }
  render(){
    return (
      <div className="App">
        <Search handler = {this.handler} searchPoint = {this.state.searchPoint}></Search>
        <Panel  searchPoint = {this.state.searchPoint} parkings = {this.state.parkings}></Panel>
      </div>
    );
  }
}

export default App;
