import L from 'leaflet';

const parkingIcon = function(feature, latlng){ 
    let icon = new L.Icon({
    iconUrl: require('./img/parking.jpg'),
    iconRetinaUrl: require('./img/parking.jpg'),
    iconAnchor: null,
    popupAnchor: [-3, -76],
    shadowUrl: null,
    shadowSize: null,
    shadowAnchor: null,
    iconSize: new L.Point(30, 31),
    className: 'leaflet-div-icon'
});
return L.marker(latlng, {icon: icon});


}

export { parkingIcon };