import {getDistance} from 'geolib';


function convertToLonLat(geojson) {
    return {
        latitude: geojson.geometry.coordinates[0],
        longitude: geojson.geometry.coordinates[1]
    }
}

function distance(p1, p2) {
    return getDistance(
        convertToLonLat(p1),
        convertToLonLat(p2)
    )
}


export {distance}