import React, {Component} from "react"
import {Form} from "react-bootstrap"
import "./searchComponent.css"
import {NominatimJS} from "nominatim-search"
import * as GeoJSONParser from "geojson"
import ParkStatus from "../shared/parkStatus"
import parking from "../shared/parking.json"
import {distance} from "../shared/geometryLib"
import * as levenshtein from "js-levenshtein"
import alias from "../shared/alias.json"
import FuzzySearch from 'fuzzy-search';
import * as $ from "jquery";

class Search extends Component{
    constructor(props){
        super(props)
        this.state = {}
        this.locateAddress = this.locateAddress.bind(this);
        this.submitHandler = this.submitHandler.bind(this);
    }
    async similarWord(word){
            const searcher = new FuzzySearch(alias,["alias"],{
                sort : true
            });
            const result = await searcher.search(word);
            if (result.length > 0){
                let address = result[0].address
                $("#searchInput").val(address)
                return result[0].address
            }
            return null
    }
    async submitHandler(event){
        event.preventDefault();
        this.locateAddress(event.target.elements[0].value);
    }
    async locateAddress(address){
        let results = await NominatimJS.search({
            street: address,
            city : "תל אביב"
          });
          if(results.length > 0 && results[0].class !== "buildings"){
            let point = GeoJSONParser.parse(results[0],{Point:["lat","lon"]})
            let  closest = await this.closetParks(point)
            this.props.handler({searchPoint:point, parkings: closest})
          }
          else{
              let addressNew = await this.similarWord(address);
              if (addressNew){
                  this.locateAddress(addressNew)
              }
              else{
                alert("לא נמצאו תוצאות")
              }
          }
    }
    async closetParks(point){
        let _parking = Object.assign({}, parking);
        let boolArray =  await Promise.all(_parking.features.map(async (item,index) => {
            let dist = distance(point,item);
            if (dist < 1200){
                let status = await ParkStatus.isFree(item.properties.parkingid);
                _parking.features[index].properties.distance = dist;
                return status;
            }
            return false;
        }))
        _parking.features = _parking.features.filter((item,index) => {
           return boolArray[index];
        })
        return _parking;
    }
    render(){
        return (
            <div className = "container searchbar">
                <div className = "row">
                    <div className = "col mt-3 mb-3">
                        <Form className="form-inline md-form form-sm mt-0 justify-content-center" props = {this.props.centerPoint} onSubmit={this.submitHandler}>
                            <i className="fa fa-search" aria-hidden="true"></i>
                            <input id = "searchInput" name="searchInput" className="form-control form-control-sm ml-3 w-75" type="text" placeholder="Search"
                                aria-label="Search"/>
                        </Form>
                    </div>
                </div>
            </div>
        )
    }
}

export default Search;